import React, { Component } from 'react';
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import '../css/App.css';
import AppBody from './AppBody';
import SpeedInfo from './SpeedInfo';
import LightInfo from './LightInfo';
import Grid from '@material-ui/core/Grid';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { connect } from 'react-redux';
import { config } from '../config';
import {Helmet} from 'react-helmet';

class App extends Component {

  statusToString(status) {

      switch(status) {
          case 0:
              return 'No connection'
          case 1:
              return 'Connected to broker (but not ' + config.train.name + ')'
          case 2:
              return 'Connected to ' + config.train.name
          case 3:
              return 'Disconnected by broker'
          case 4:
              return 'Disconnected (lost connection)'
          case 5:
              return 'Server Error'
          default:
              return 'Unknown (no connection).'
      }

  }

  render() {
    return (
      <div className="App">
        <Helmet>
              <style>{'body { background-image: url("/img/bg.png"); }'}</style>
        </Helmet>
        <AppBar position="static">
              <Toolbar>
                  <Typography variant="title">
                      FogLight Train
                  </Typography>
                  <Button>Status: {this.statusToString(this.props.connectionStatus)}</Button>
              </Toolbar>
        </AppBar>
        <AppBody>
            <Grid container spacing={24}>
                <Grid item xs={6}>
                    <SpeedInfo/>
                </Grid>
                <Grid item xs={6}>
                    <LightInfo/>
                </Grid>
            </Grid>
        </AppBody>
      </div>
    );
  }
}

const mapStateToProps = state => {
    return {
        connectionStatus: state.global.status
    };
};

export default connect(mapStateToProps)(App);



