import { Gauge } from 'gaugeJS';
import React from 'react';

class TrainGauge extends React.Component {

    componentDidMount() {
        this.gauge = new Gauge(this.refs.canvas)
        this.gauge.animationSpeed = 1
        this.gauge.minValue = this.props.min
        this.gauge.maxValue = this.props.max
        this.gauge.setOptions(this.props.opts)
    }

    componentDidUpdate(prevProps, prevState) {
        this.gauge.set(this.props.value)
    }

    render() {
        return (
           <canvas ref="canvas" width="500" height="400"></canvas>
        );
    }
}

export default TrainGauge;


