import React from 'react';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Gauge from './TrainGauge';
import Slider from '@material-ui/lab/Slider';
import { connect } from 'react-redux';
import Grid from '@material-ui/core/Grid';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import { config } from '../config';

class SpeedInfo extends React.Component {

    onChangeSlider = (event, value) => {
        this.props.updateEnginePower(value)
    }

    render() {
        return (
            <Paper className="grid-paper" style={{padding:20}}>
                <Typography variant="headline" component="h3">
                    Speed
                </Typography>
                <Grid container justify="center">
                    <Gauge min="-100" max="100" value={this.props.power.actual} opts={
                    {
                        angle: -0.3,
                        lineWidth: 0.2,
                        pointer: {
                            length: 0.5,
                            strokeWidth: 0.035,
                            color: '#000000'
                        },
                        staticZones: [
                            {strokeStyle: '#ff583d', min: -100, max: -50},
                            {strokeStyle: '#ffe268', min: -50, max: 50},
                            {strokeStyle: '#84cc59', min: 50, max: 100},
                        ],
                        renderTicks: {
                            divisions: 10,
                            divWidth: 1.1,
                            divLength: 0.3,
                            divColor: '#333333',
                            subDivisions: 8,
                            subLength: 0.2,
                            subWidth: 0.2,
                            subColor: '#666666'
                        },
                        staticLabels: {
                            font: "11px 'Lato', sans-serif",
                            labels: [-100, -80, -60, -40, -20, 0, 20, 40, 60, 80, 100],
                            color: "#000000"
                        }
                    } }
                />
                </Grid>
                <FormControl component="fieldset" style={{width:'100%'}}>
                    <FormLabel component="legend">Speed</FormLabel>
                    <Slider min={-1000} max={1000} value={this.props.power.targeted} aria-labelledby="label" onChange={this.onChangeSlider}/>
                </FormControl>
            </Paper>
        );
    }
}

const mapStateToProps = state => {
    return {
        power: {
            actual: state.engine.power.actual,
            targeted: state.engine.power.targeted
        }
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        updateEnginePower: (speed) => {
            // send message to MQTT.
            let msg = new Buffer(8)

            msg.writeInt32BE(speed) // numerator
            msg.writeInt32BE(1000, 4) // denominator

            dispatch({
                type: 'UPDATE_MQTT_ENGINE_POWER',
                mqtt: {
                    topic: config.train.name + '/engine/power/control',
                    payload: msg
                }
            })
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SpeedInfo);



