import React from 'react';
import ReactDOM from 'react-dom';
import App from './components/App';
import './css/index.css';

import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider';
import createMuiTheme from '@material-ui/core/styles/createMuiTheme';

import { Provider } from 'react-redux';
import { createStore, applyMiddleware } from 'redux';
import { createClient } from './lib/mqttredux';

import reducer from './reducer';

import { config } from './config';

const mqttConfig = {
  url: 'ws://' + config.train.name + '.local:' + config.mqtt.port,
  opt: {
    clientId: config.mqtt.clientId,
    protocolId: 'MQIsdp',
    protocolVersion: 3,
  },
};

const mqttRedux = createClient(mqttConfig);
const store = createStore(reducer, applyMiddleware(mqttRedux.createMiddleware()));

const actionTopicMapping = {
  'AMBIENT_LIGHT_FEEDBACK': config.train.name + '/lights/ambient/feedback',
  'ENGINE_POWER_FEEDBACK': config.train.name + '/engine/power/feedback',
  'LIGHTS_OVERRIDE_FEEDBACK': config.train.name + '/lights/override/feedback'
};

mqttRedux.connect(actionTopicMapping, store);

const theme = createMuiTheme({
    palette: {
        primary: {
            main: '#efefef',
        }
    },
});

document.title = 'FogLight - ' + config.train.name;
ReactDOM.render(
  <Provider store={store}>
    <MuiThemeProvider theme={theme}>
      <App />
    </MuiThemeProvider>
  </Provider>,
  document.getElementById('root')
);



