import React from 'react';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Gauge from './TrainGauge';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import Grid from '@material-ui/core/Grid';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import { config } from '../config';
import { connect } from 'react-redux';

class LightInfo extends React.Component {

    onChangeLightMode = (event, value) => {
        this.props.updateLightMode(value)
    }

    render() {
        return (
            <Paper className="grid-paper" style={{padding:20}}>
                <Typography variant="headline" component="h3">
                    Light Sensor
                </Typography>
                <Grid container justify="center">
                    <Gauge min="0" max="256" value={this.props.ambient} opts={
                        {
                            angle: -0.3,
                            lineWidth: 0.2,
                            pointer: {
                                length: 0.5,
                                strokeWidth: 0.035
                            },
                            staticZones: [
                                {strokeStyle: '#233c95', min: 0, max: 128},
                                {strokeStyle: '#e2b83a', min: 128, max: 256},
                            ],
                            highDpiSupport: true,
                            renderTicks: {
                                divisions: 8,
                                divWidth: 1.1,
                                divLength: 0.3,
                                divColor: '#333333',
                                subDivisions: 4,
                                subLength: 0.2,
                                subWidth: 0.2,
                                subColor: '#666666'
                            },
                            staticLabels: {
                                font: "11px 'Lato', sans-serif",
                                labels: [0, 32, 64, 96, 128, 160, 192, 224, 256],
                                color: "#000000"
                            }
                        }}
                    />
                </Grid>
                <div>
                    <FormControl component="fieldset">
                        <FormLabel component="legend">Headlight</FormLabel>
                        <RadioGroup
                            aria-label="light"
                            name="Light"
                            value={this.props.mode}
                            onChange={this.onChangeLightMode}
                        >
                            <FormControlLabel value="auto" control={<Radio/>} label="Automatic"/>
                            <FormControlLabel value="on" control={<Radio/>} label="On"/>
                            <FormControlLabel value="off" control={<Radio/>} label="Off"/>
                        </RadioGroup>
                    </FormControl>
                </div>
            </Paper>
        )
    }
}

const mapStateToProps = state => {
    return {
        ambient: state.light.ambient,
        mode: state.light.mode
    };
};


const mapDispatchToProps = (dispatch) => {
    return {
        updateLightMode: (mode) => {

            var numMode;

            switch(mode) {
                case 'off':
                    numMode = 0;
                    break;
                case 'on':
                    numMode = 1;
                    break;
                default:
                    numMode = 2;
            }

            // Send the MQTT message.
            let msg = new Buffer(4)
            msg.writeInt32BE(numMode)
            dispatch({
                type: 'UPDATE_MQTT_LIGHT_MODE',
                mqtt: {
                    topic: config.train.name + '/lights/override/control',
                    payload: msg
                }
            })
        }
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(LightInfo);



