let ConnectionStatus = {
    NO_CONNECTION: 0,
    HAS_BROKER: 1,
    HAS_TRAIN: 2,
    DISCONNECTED_BY_BROKER: 3,
    LOST_CONNECTION: 4,
    SERVER_ERROR: 5
};

const defaultState = {
    global: {
        status: ConnectionStatus.NO_CONNECTION
    },
    light: {
        ambient: 0,
        mode: 'auto'
    },
    engine: {
        power: {
            actual: 0,
            targeted: 0
        }
    }
};

export default function (state = defaultState, action) {
    switch (action.type) {
        case 'AMBIENT_LIGHT_FEEDBACK':
            let ambientPayload = Buffer.from(action.payload);
            let light = ambientPayload.readInt32BE(0);

            return {
                ...state,
                global: {
                    status: ConnectionStatus.HAS_TRAIN
                },
                light: {ambient: light, mode: state.light.mode}
            };
        case 'LIGHTS_OVERRIDE_FEEDBACK':
            let modePayload = Buffer.from(action.payload);
            let numMode = modePayload.readInt32BE(0);

            var mode;

            switch(numMode) {
                case 0:
                    mode = 'off';
                    break;
                case 1:
                    mode = 'on';
                    break;
                default:
                    mode = 'auto';
                    break;
            }

            return {
                ...state,
                light: {
                    ambient: state.light.ambient,
                    mode: mode }
            };
        case 'ENGINE_POWER_FEEDBACK':
            let enginePayload = action.payload;

            let engineBuffer = Buffer.from(enginePayload);

            // Rational is first 4 bytes: numerator, next 4 bytes: denominator
            // We need to normalize it to our -100 to 100 scale, so divide and multiply
            let speed = engineBuffer.readInt32BE(0, 3) / engineBuffer.readInt32BE(4, 7) * 100;

            return {
                ...state,
                global: {
                    status: ConnectionStatus.HAS_TRAIN
                },
                engine: {
                    power: {
                        actual: speed,
                        targeted: speed * 10 //speed slider is -1000, 1000, so multiply speed by 10
                    }
                }
            };
        case 'MQTT_CONNECT':
            return {
                ...state,
                global: {
                    status: ConnectionStatus.HAS_BROKER
                },
            };

        case 'MQTT_DISCONNECT':
            return {
                ...state,

                global: {
                    status: ConnectionStatus.DISCONNECTED_BY_BROKER
                },
            };

        case 'MQTT_ERROR':
            return {
                ...state,

                global: {
                    status: ConnectionStatus.SERVER_ERROR
                },
            };

        default:
            return state;
    }
};



